import { Util } from "@nuka9510/js-util";
import Phase from "./enums/phase.js";
/**
 * Validation CheckÎ•º ÏúÑÌïú Í∞ùÏ≤¥
 */
export default class Validation {
    /** Í≤∞Í≥º Í∞í Í∞ùÏ≤¥ */
    result;
    /** validation checkÌï† ElementÎ•º Îã¥Îäî Í∞ùÏ≤¥ */
    #el;
    /** validation checkÌï† radio ElementÎ•º Îã¥Îäî Í∞ùÏ≤¥ */
    #radio;
    /** validation checkÏóê ÏÇ¨Ïö©Ìï† Ï†ïÍ∑úÏãùÏùÑ Îã¥ÏùÄ Í∞ùÏ≤¥ */
    #regex;
    /**
     * Validation CheckÎ•º ÏúÑÌïú Í∞ùÏ≤¥
     *
     * ```
     * <form name="form">
     *   <input type="text" name="text" data-sv-pattern="password" data-sv-input-name="ÎπÑÎ∞ÄÎ≤àÌò∏" minlength="0" maxlength="10">
     *   <input type="text" name="text" data-sv-pattern="password" minlength="0" maxlength="10" required="ÎπÑÎ∞ÄÎ≤àÌò∏">
     *   <input type="date" name="sdate1" data-sv-date="date1" data-sv-date-state="S" data-sv-input-name="Í≤ÄÏÉâÏùº1">
     *   <input type="date" name="edate1" data-sv-date="date1" data-sv-date-state="E" data-sv-input-name="Í≤ÄÏÉâÏùº1">
     *   <input type="date" name="sdate2" data-sv-date="date2" data-sv-date-state="S" required="Í≤ÄÏÉâÏùº2">
     *   <input type="date" name="edate2" data-sv-date="date2" data-sv-date-state="E" required="Í≤ÄÏÉâÏùº2">
     * </form>
     * <script type="importmap">
     *   {
     *     "imports": {
     *       "@nuka9510/js-util": "https://cdn.jsdelivr.net/npm/@nuka9510/js-util/dist/index.js",
     *       "@nuka9510/simple-validation": "https://cdn.jsdelivr.net/npm/@nuka9510/simple-validation/dist/index.js"
     *     }
     *   }
     * </script>
     * <script type="module">
     *   import { SValidation } from "@nuka9510/simple-validation";
     *
     *   const validation = new SValidation({regex: {password: /^[\S!?@#$%^&*():;+-=~{}<>\_\[\]\|\\\"\'\,\.\/\`]{6,10}$/}});
     *
     *   validation.run(form);
     *
     *   if (validation.result.flag) {
     *     form.submit();
     *   } else {
     *     alert(validation.result.alertMsg);
     *     validation.result.el.focus();
     *   }
     * </script>
     * ```
     */
    constructor(config) { this.init(config); }
    /** Í∞ùÏ≤¥ Ï¥àÍ∏∞Ìôî */
    init(
    /** validation Ï¥àÍ∏∞ÌôîÎ•º ÏúÑÌïú Í∞ùÏ≤¥ */ config = null) {
        this.#resultInit();
        this.#elInit();
        this.#radioInit();
        this.#regexInit(config?.regex);
    }
    /** Í≤∞Í≥º Í∞í Ï¥àÍ∏∞Ìôî */
    #resultInit() {
        this.result = {
            flag: true,
            alertMsg: null,
            el: null,
            phase: Phase.INIT
        };
    }
    /** validation checkÌï† ElementÎ•º Îã¥Îäî Í∞ùÏ≤¥ Ï¥àÍ∏∞Ìôî */
    #elInit() { this.#el = {}; }
    /** validation checkÌï† radio ElementÎ•º Îã¥Îäî Í∞ùÏ≤¥ Ï¥àÍ∏∞Ìôî */
    #radioInit() { this.#radio = {}; }
    /** validation checkÏóê ÏÇ¨Ïö©Ìï† Ï†ïÍ∑úÏãùÏùÑ Îã¥ÏùÄ Í∞ùÏ≤¥ Ï¥àÍ∏∞Ìôî */
    #regexInit(regex = null) {
        this.#regex = (!Util.empty(regex) &&
            Util.isObject(regex))
            ? {
                ...this.#regex,
                ...regex
            }
            : { ...this.#regex };
    }
    /** elÏóê ÏûàÎäî ElementÎì§ÏùÑ required checkÌïúÎã§.  */
    #required(el) {
        const required = el.getAttribute('required');
        if (!Util.empty(required)) {
            if (el.type == 'radio') {
                this.#setRadio(el);
            }
            else if (Util.empty(el.value)) {
                this.result.flag = false;
                this.result.alertMsg = `'${required}'ÏùÑ/Î•º ÏûÖÎ†•Ìï¥ Ï£ºÏÑ∏Ïöî.`;
                this.result.el = el;
                this.result.phase = Phase.REQUIRED;
            }
        }
    }
    /** radioÏóê ÏûàÎäî ElementÎì§ÏùÑ required checkÌïúÎã§.  */
    #requiredRadio() {
        for (const i in this.#radio) {
            const el = this.#radio[i][0], flag = this.#radio[i].some((...arg) => arg[0].checked);
            if (!flag) {
                this.result.flag = false;
                this.result.alertMsg = `'${i}'ÏùÑ/Î•º ÏÑ†ÌÉùÌï¥Ï£ºÏÑ∏Ïöî.`;
                this.result.el = el;
                this.result.phase = Phase.REQUIRED;
                break;
            }
        }
    }
    /** elÏóê ElementÎ•º Îã¥ÎäîÎã§.  */
    #setEl(el) {
        const pattern = el.dataset['svPattern'], date = el.dataset['svDate'];
        if (!Util.empty(pattern)) {
            if (Util.empty(this.#el.el)) {
                this.#el.el = [];
            }
            this.#el.el?.push(el);
        }
        if (!Util.empty(date)) {
            const state = el.dataset['svDateState'];
            switch (state) {
                case 'S':
                case 'E':
                    if (Util.empty(this.#el.date)) {
                        this.#el.date = {};
                    }
                    if (Util.empty(this.#el.date[date])) {
                        this.#el.date[date] = {};
                    }
                    this.#el.date[date][state] = el;
                    break;
            }
        }
    }
    /** `#radio`Ïóê typeÏù¥ 'radio'Ïù∏ ElementÎ•º Îã¥ÎäîÎã§.  */
    #setRadio(el) {
        const required = el.getAttribute('required');
        if (!Util.empty(required)) {
            if (Util.empty(this.#radio[required])) {
                this.#radio[required] = [el];
            }
            else {
                this.#radio[required].push(el);
            }
        }
    }
    /**
     * ElementÎì§ÏùÑ validation check ÌïúÎã§.
     * ```
     * -----------------------
     * date : isDate
     * -----------------------
     * el : isPattern
     * ```
     */
    #match() {
        for (const i in this.#el) {
            if (this.result.flag) {
                switch (i) {
                    case 'date':
                        this.#isDate(this.#el[i]);
                        break;
                    case 'el':
                        this.#isPattern(this.#el[i]);
                        break;
                }
            }
            else {
                break;
            }
        }
    }
    /** date check */
    #isDate(el) {
        for (const i in el) {
            if (this.result.flag) {
                const sdate = el[i].S.value, edate = el[i].E.value;
                if (!Util.empty(sdate) &&
                    !Util.empty(edate)) {
                    const inputName = el[i].S.dataset['svInputName'] ||
                        el[i].E.dataset['svInputName'], required = el[i].S.getAttribute('required') ||
                        el[i].E.getAttribute('required');
                    if ((new Date(sdate)).getTime() > (new Date(edate)).getTime()) {
                        this.result.flag = false;
                        this.result.alertMsg = `'${inputName || required}'Ïùò ÏãúÏûëÏùºÏù¥ Ï¢ÖÎ£åÏùº Î≥¥Îã§ Îä¶ÏäµÎãàÎã§.`;
                        this.result.el = el[i].S;
                        this.result.phase = Phase.MATCH;
                    }
                }
            }
            else {
                break;
            }
        }
    }
    /** regex check */
    #isPattern(el) {
        if (Array.isArray(el)) {
            for (const i of el) {
                const pattern = i.dataset['svPattern'], inputName = i.dataset['svInputName'], required = i.getAttribute('required'), val = i.value;
                if (Object.keys(this.#regex).includes(pattern)) {
                    if (!Util.empty(val) &&
                        !this.#regex[pattern].test(val)) {
                        this.result.flag = false;
                        this.result.alertMsg = `'${inputName || required}'Ïùò ÌòïÏãùÏù¥ Ïò¨Î∞îÎ•¥ÏßÄ ÏïäÏäµÎãàÎã§.`;
                        this.result.el = i;
                        this.result.phase = Phase.MATCH;
                        break;
                    }
                }
            }
        }
        else {
            const pattern = el.dataset['svPattern'], inputName = el.dataset['svInputName'], required = el.getAttribute('required'), val = el.value;
            if (Object.keys(this.#regex).includes(pattern)) {
                if (!Util.empty(val) &&
                    !this.#regex[pattern].test(val)) {
                    this.result.flag = false;
                    this.result.alertMsg = `'${inputName || required}'Ïùò ÌòïÏãùÏù¥ Ïò¨Î∞îÎ•¥ÏßÄ ÏïäÏäµÎãàÎã§.`;
                    this.result.el = el;
                    this.result.phase = Phase.MATCH;
                }
            }
        }
    }
    /** Element valueÏùò lengthÎ•º check ÌïúÎã§.  */
    #length() {
        for (const i in this.#el) {
            if (i == 'el' &&
                this.result.flag) {
                for (const j of this.#el[i]) {
                    const inputName = j.dataset['svInputName'], required = j.getAttribute('required'), val = j.value.length;
                    if (!(j instanceof HTMLSelectElement)) {
                        if (j.minLength >= 0 &&
                            j.maxLength >= 0) {
                            if (val < j.minLength ||
                                val > j.maxLength) {
                                this.result.flag = false;
                                this.result.alertMsg = `'${inputName || required}'ÏùÄ/Îäî ${j.minLength}~${j.maxLength}Ïûê Ïù¥ÎÇ¥Î°ú ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.`;
                                this.result.el = j;
                                this.result.phase = Phase.LENGTH;
                                break;
                            }
                        }
                        else if (j.minLength >= 0 &&
                            j.maxLength < 0) {
                            if (val < j.minLength) {
                                this.result.flag = false;
                                this.result.alertMsg = `'${inputName || required}'ÏùÄ/Îäî ${j.minLength}Ïûê Ïù¥ÏÉÅÏúºÎ°ú ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.`;
                                this.result.el = j;
                                this.result.phase = Phase.LENGTH;
                                break;
                            }
                        }
                        else if (j.minLength < 0 &&
                            j.maxLength >= 0) {
                            if (val > j.maxLength) {
                                this.result.flag = false;
                                this.result.alertMsg = `'${inputName || required}'ÏùÄ/Îäî ${j.maxLength}Ïûê Ïù¥ÌïòÎ°ú ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.`;
                                this.result.el = j;
                                this.result.phase = Phase.LENGTH;
                                break;
                            }
                        }
                    }
                }
            }
            else if (!this.result.flag) {
                break;
            }
        }
    }
    /** validationÏùÑ Ïã§ÌñâÌïúÎã§. */
    run(form) {
        this.init();
        for (const el of form.elements) {
            if (this.result.flag) {
                if (['INPUT', 'SELECT', 'TEXTAREA'].includes(el.tagName)) {
                    if (!el.disabled) {
                        this.#required(el);
                        this.#setEl(el);
                    }
                }
            }
            else {
                break;
            }
        }
        if (this.result.flag) {
            this.#requiredRadio();
        }
        if (this.result.flag) {
            this.#match();
        }
        if (this.result.flag) {
            this.#length();
        }
        if (this.result.flag) {
            this.result.phase = Phase.DONE;
        }
    }
}
